* {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

body{
  padding: 40px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: black;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card > img {
  height: 250px;
  min-width: 250px;
  object-position: center;
  object-fit: cover;
  width: 100%;
}

.card p {
  padding: 5px;
  text-align: center;
}